.career_collapse {
  margin-top: 65px;
  padding-left: calc((100% - 880px) / 2);
  padding-right: calc((100% - 880px) / 2);
  > :first-child {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 25px;
  }
  > .ant-collapse {
    font-size: 20px;
    color: rgba(30, 30, 30, 1);
  }
  .ant-collapse-ghost > .ant-collapse-item {
    border-bottom: 1px solid #1e1e1e33;
  }

  p {
    font-size: 16px;
  }
}

.table {
  margin: auto;
}

.panel_title {
  font-weight: bolder;
  font-size: 16px;
  margin-top: 20px;
  margin-bottom: 15px;
}
.panel_title {
  &:nth-child(1) {
    margin-top: 0;
  }
}
.team_title {
  font-weight: bold;
  margin-top: 40px;
}

.ant-collapse-header {
  padding: 12px 0 !important;
}

.career_list {
  background-color: #fffbfc;
  margin: 60px auto 0;
  padding-top: 90px !important;
  padding-bottom: 120px !important;
  padding-left: calc((100% - 860px) / 2);
  padding-right: calc((100% - 860px) / 2);
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  > :nth-child(2) {
    > div {
      &:first-child {
        margin-top: 35px;
      }
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
      @media (max-width: 768px) {
        display: block;
        margin-bottom: 0px;
      }
      > div {
        width: 47%;
        height: 55px;
        line-height: 55px;
        background-color: #fff;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.1);
        border-radius: 12px;
        @media (max-width: 768px) {
          width: 90%;
          margin: 20px auto 0;
        }
      }
    }
  }
  @media (max-width: 768px) {
    width: 100%;
    display: block;
    padding: 0;
  }
}
